<template>
  <div class="hello">
    <h1>FAKE FLIX</h1>
    <Navbar/>
    <h2>Votre Watchlist</h2>
    <div id="movie_container">
      <img v-for="el of fav_movies" :key="el.id" :src="el" alt />
    </div>
    <Film/>
  </div>
</template>

<script>
// @ is an alias to /src
import Navbar from '@/components/Navbar.vue';
import Film from '@/components/Film.vue';
import store from "./Home.js";

export default {
  store: store,
  name: 'Favoris',
  data: function() {
    return {
      fav_movies: []
    };
  },
  components: {
      Navbar,
      Film
    },
  computed: {
    session_id: function() {
      return store.state.session_id
    },
    
    getsession_id(){
      return this.session_id
    }
  }, 
  created: function () {
      this.getsession_Id()
    },
  methods:{
    getsession_Id(){
      fetch(
        `https://api.themoviedb.org/3/account/{account_id}/watchlist/movies?api_key=b94d0075dc634f6f48d56ae29e58f3ce&language=en-US&session_id=${this.session_id}&sort_by=created_at.asc&page=1`
      )
        .then(response => {
          return response.json().then(data => {
            console.log("Données Jsonnizées");
            console.log(data);
              for (const element of data.results) {
              this.fav_movies.push(
                `https://image.tmdb.org/t/p/w200${element.poster_path}`,
                element.id
              );
            }
          });
        })
        .catch(function(error) {
          console.log("Une erreur s'est produite");
          console.log(error);
        });
    }
  }
  
}
</script>

